function Message(props) {
    const { text, uid, photoURL } = props.message;

    const messageClass = uid === props.currentUser.uid ? 'sent' : 'received';

    return (
        <div style={message} className={`${messageClass}`}>
            <img src={photoURL} style={photo} alt='' />
            <p style={{ borderRadius: '1.2em', padding: '.45em .85em', fontSize: '.9em', fontFamily: 'Roboto' }}>{text}</p>
        </div>
    )
}

const message = {
    height: '2.25em',
    fontSize: '1em',
    display: 'flex',
    alignItems: 'center',
    margin: '.6em .5em'
}

const photo = {
    height: '100%',
    borderRadius: '50%'
}

export default Message;