import ActiveForm from "./ActiveForm";
import InactiveForm from "./InactiveForm";

function Bottom({ user, value, setValue, sendMessage }) {
    return (
        <div style={{ width: '100%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            {user ? <ActiveForm input={input} value={value} setValue={setValue} sendMessage={sendMessage} /> : <InactiveForm input={input} />}
        </div>
    )
}

const input = {
    width: '75%',
    height: '100%',
    outline: 'none',
    border: 'none',
    borderRadius: '.25em',
    padding: '0 .75em',
    fontFamily: 'Roboto',
    fontSize: '.75em'
}

export default Bottom;