function ActiveForm({ input, value, setValue, sendMessage }) {
    return (
        <form onSubmit={sendMessage} style={{ width: '87%', height: '2em', display: 'flex', justifyContent: 'space-between' }}>
            <input style={input} value={value} onChange={setValue} type='text' placeholder='Enter your message...' onFocus={(e) => e.target.placeholder = ''} onBlur={(e) => e.target.placeholder = 'Enter your message...'} />
            <button className='send-button'>Send</button>
        </form>
    )
}

export default ActiveForm;