function InactiveForm({ input }) {
    return (
        <form style={{ width: '87%', height: '2em', display: 'flex', justifyContent: 'space-between', pointerEvents: 'none', opacity: '.65' }}>
            <input style={input} type='text' placeholder='Sign in to chat...' />
            <button className='send-button'>Send</button>
        </form>
    )
}

export default InactiveForm;